import utils from '../../../common/js/util.js'
export default {
	data() {
		return {
			codeDisable: false,
			show: false,
			bankDisabled: false,
			accountNo:'',//银行卡号
			code:'',//短信验证码
			bank:'',//开户行
			saveCode: '',  // 安全码
			timeCode: '', // 卡片有效期
			billDay: '',  // 账单日
			repayDay: '',  // 还款日
			mobile: '',//手机号
			disabled: false,	// 禁止点击
			bank_account_name:'',
			request_no: '',
			type: 'perfect',
			deviceID: ''
		};
	},
	onLoad(e) {
		this.type = e.type?e.type:'perfect'
		plus.device.getInfo({
			success:(e)=>{
				this.deviceID = e.uuid
			},
			fail:function(e){
			}
		});
	},
	methods:{
		submit(){
			if(!this.accountNo || !this.saveCode || !this.billDay || !this.repayDay || !this.timeCode || !this.mobile || !this.bank_account_name){
				uni.showToast({
					title: '带星号为必填',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			let data = {
				// merchant_name:this.name,
				channel_type: this.type,
				bank_cardno: this.accountNo,
				// bank_abbr:this.bank,
				bank_mobile:this.mobile,
				bank_name: this.bank_account_name,
				cvn2: this.saveCode,
				bill_date: this.billDay,
				repayment_date: this.repayDay,
				expired: this.timeCode,
				token: uni.getStorageSync('token'),
				deviceID: this.deviceID
			}
			this.disabled = true
			this.$store.dispatch('setCreditCard',data)
			.then(result=>{
				if(result.isSMS==1){
					this.show = true
					this.request_no = result.request_no
				}else{
					uni.showToast({
						title:'绑定成功',
						icon: 'none', //loading   none  success
						mask: false,
						duration: 1500,
						success: (res)=>{
							setTimeout(function(){
								this.disabled = false
								uni.navigateBack({
									delta:1
								})
							},1500)
						}
					})
				}
			}).catch(err=>{ 
				this.disabled = false
				if(err.code==3){
					uni.navigateTo({
						url: `/pages/webview/webview?url=${err.url}&msg=${JSON.stringify(err.msg)}`
					})
				}
			})			
		},
		confirm(){
			let data = {
				request_no: this.request_no,
				token: uni.getStorageSync('token'),
				smsCode: this.code
			}
			this.codeDisable = false
			this.$store.dispatch('setCreditCardTwo',data)
			.then(result=>{
				uni.showToast({
					title:'绑定成功',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500,
					success: (res)=>{
						this.show = false
						setTimeout(function(){
							this.codeDisable = false
							uni.navigateBack({
								delta:1
							})
						},1500)
					}
				})
			}).catch(err=>{ 
				this.codeDisable = false
			})	
		},
		input(){
			this.$store.dispatch('getBankInfo',{
				bank_cardno: this.accountNo
			}).then(res=>{
				this.bankDisabled = true
				this.bank_account_name = res.data.bank_name
				this.bank = res.data.bank_abbr
			}).catch(err=>{})
		},
	}
}
